.design {
  position: relative;

  &__img {
    background-image: url(./images/background-3d.jpg);
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 104px;
    background-color: #ccc;
  }

  &__title {
    font-weight: 400;
    font-size: 144px;
    line-height: 169px;
    color: #000;
    text-shadow: 0px 3px 4px rgba(0, 0, 0, .4);
  }
}

@media (max-width: 1090px) {
  .design {
    &__title {
      font-size: 94px;
      line-height: 129px;
    }
	}
}

@media (max-width: 690px) {
  .design {
    &__title {
      font-size: 54px;
      line-height: 89px;
    }

    &__img {
      padding-top: 80px;
    }
  }
}